import {
  Component,
  computed,
  DestroyRef,
  inject,
  OnInit,
  signal,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ActivatedRoute, Data } from '@angular/router';
import {
  catchError,
  combineLatest,
  EMPTY,
  from,
  of,
  switchMap,
  tap,
  throwError,
} from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

// Third-party imports
import { AccordionModule } from 'primeng/accordion';
import { DividerModule } from 'primeng/divider';
import { ProgressSpinnerModule } from 'primeng/progressspinner';

// Application imports
import { DistributorResponse } from '../../../../../../api';
import { DistributorDataStrategy } from '../../../../strategies';
import { AdminDetailSection } from '../../../../models';
import {
  isDefined,
  isEmpty,
  isNullOrUndefined,
} from '../../../../../../shared/utils';
import { SectionConfigBuilder } from '../../../../factories';
import { BroadcastEventMessageService } from '../../../../services/broadcast-event-messages.service';
import { FeedSectionComponent } from '../../../shared/feed-section/feed-section.component';

interface DistributorRouteData extends Data {
  distributor?: DistributorResponse;
}

interface EntityUpdatedMessage {
  entityType: string;
  data: DistributorResponse;
}

@Component({
  selector: 'app-distributor-details-container',
  imports: [
    AccordionModule,
    DividerModule,
    ProgressSpinnerModule,
    CommonModule,
  ],
  templateUrl: './distributor-details-container.component.html',
  styleUrl: './distributor-details-container.component.scss',
})
export class DistributorDetailsContainerComponent implements OnInit {
  private readonly broadcastService = inject(BroadcastEventMessageService);
  protected informationSections = signal<AdminDetailSection[]>([]);
  protected settingsSections = signal<AdminDetailSection[]>([]);
  protected statisticsSections = signal<AdminDetailSection[]>([]);
  protected changesSections = signal<AdminDetailSection[]>([]);
  protected loading = signal(true);
  protected entity = signal<DistributorResponse | null>(null);
  protected error = signal<string | null>(null);
  protected defaultExpandedSections = 0;

  // Helper computed properties
  protected hasInformationSections = computed(
    () => this.informationSections().length > 0,
  );
  protected hasSettingsSections = computed(
    () => this.settingsSections().length > 0,
  );
  protected hasStatisticsSections = computed(
    () => this.statisticsSections().length > 0,
  );
  protected hasChangesSections = computed(
    () => this.changesSections().length > 0,
  );

  // Services
  private readonly dataStrategy = inject(DistributorDataStrategy);
  private readonly destroyRef = inject(DestroyRef);
  private readonly route = inject(ActivatedRoute);

  private isEntityUpdatedMessage(
    message: unknown,
  ): message is EntityUpdatedMessage {
    return (
      isDefined(message) &&
      message !== null &&
      typeof message === 'object' &&
      'entityType' in message &&
      'data' in message &&
      (message as EntityUpdatedMessage).entityType === 'distributor'
    );
  }
  ngOnInit(): void {
    this.initializeEntityData();
    this.broadcastService
      .on('entity-updated')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        if (this.isEntityUpdatedMessage(message)) {
          this.entity.set(message.data);
          this.updateSectionsWithEntityData();
        }
      });
  }

  private initializeEntityData(): void {
    combineLatest([
      this.route.paramMap,
      this.route.parent?.parent?.data || of<DistributorRouteData>({}),
    ])
      .pipe(
        takeUntilDestroyed(this.destroyRef),
        tap(() => {
          this.loading.set(true);
          this.setupSectionStructure();
        }),
        switchMap(([params, data]) => {
          const distributorId =
            params.get('distributorId') ?? data['distributor']?.id;

          if (isDefined(distributorId)) {
            return from(this.dataStrategy.fetchById(distributorId));
          }

          if (!isEmpty(data['distributor'])) {
            return of(data['distributor']);
          }

          return throwError(() => new Error('Distributor ID not found'));
        }),
        catchError(() => {
          this.error.set('Failed to load distributor data');
          return EMPTY;
        }),
        tap((distributorData) => {
          this.entity.set(distributorData);
          this.loading.set(false);
          this.updateSectionsWithEntityData();
        }),
      )
      .subscribe();
  }

  private setupSectionStructure(): void {
    // Register information sections
    this.informationSections.set(
      SectionConfigBuilder.createDistributorInformationSections(null),
    );

    // Register settings sections
    this.settingsSections.set(
      SectionConfigBuilder.createDistributorSettingsSections(null),
    );
  }

  private updateSectionsWithEntityData(): void {
    const entityData = this.entity();
    if (isNullOrUndefined(entityData)) return;

    // Update sections with entity data
    this.informationSections.set(
      SectionConfigBuilder.createDistributorInformationSections(entityData),
    );

    this.settingsSections.set(
      SectionConfigBuilder.createDistributorSettingsSections(entityData),
    );

    this.statisticsSections.set(
      SectionConfigBuilder.createDistributorStatisticsSections(entityData),
    );

    this.changesSections.set([
      {
        id: 'distributor-changes',
        title: 'Changes',
        component: FeedSectionComponent,
        componentInputs: {
          entity: entityData,
          entityType: 'distributor',
        },
        category: 'feed',
        order: 10,
      } as AdminDetailSection,
    ]);
  }

  // Method for child components to save data
  async updateEntity(updates: Partial<DistributorResponse>): Promise<void> {
    const distributor = this.entity();
    if (isNullOrUndefined(distributor)) return;

    try {
      this.loading.set(true);
      const updatedEntity = await this.dataStrategy.update(
        distributor.id,
        updates,
      );
      this.entity.set(updatedEntity);
    } catch (error) {
      this.error.set(
        `${error instanceof Error ? error.message : String(error)}`,
      );
    } finally {
      this.loading.set(false);
    }
  }

  // Helper method to combine inputs
  protected getSectionInputs(
    section: AdminDetailSection,
  ): Record<string, unknown> {
    return {
      ...section.componentInputs,
      /*   saveHandler: (data: Partial<DistributorResponse>) => this.updateEntity(data), */
    };
  }
}
